.main-header {
    padding-top: 64px;
    // position: relative
    padding-left: 20px;
    padding-right: 20px;
    width: 100%;
    // background: cadetblue
    z-index: 900;

    @media screen and (min-width: 1430px + 13px) { // 13
        position: absolute; }
    @media screen and (min-width: 1460px + 13px) { // 13
        position: absolute; }
    @media screen and (min-width: 1500px + 13px) {
        position: absolute;
        padding-left: 40px;
        padding-right: 40px; }
    @media screen and (min-width: 1580px + 13px) {
        position: absolute;

        padding-left: 80px;
        padding-right: 80px; }

    .main-header__logo {
        // position: absolute
        // left: 80px
        z-index: 100;
        img {
            width: 92px;
            @include media-breakpoint-down(md) {
                width: 80px; } } }
    .main-header__menu {
        // position: absolute
        // right: 64px
        // top: 56px
        top: -8px;
        // padding-right: 33px
        position: relative;
        width: 140px;
        z-index: 100;
        &::before {
            content: "";
            // border: 1px solid $color--main
            position: absolute;
            right: -1px;
            top: 8px;
            // height: 18px
            height: 16px;
            width: 1.4px;
            background: $color--main; }
        menu {
            padding: 0px;
            margin: 0px; }
        li {
            list-style: none;
            margin-bottom: 13px;
            &:last-child {
                margin-bottom: 0px; }
            a {
                font-size: rem(16);
                line-height: rem(16);
                color: $color--main;
                padding: 8px 16px;
                display: inline-block;
                text-decoration: none;
                &:hover,
                &.active {
                    background: $color--main;
                    color: #fff; } } } }

    @include media-breakpoint-down(md) {
        position: relative;
        padding-top: 24px;
        padding-left: 20px;
        padding-right: 20px;
        .main-header__menu {
            width: 98px;
            li {
                margin-bottom: 10px;
                a {
                    padding: 8px 8px;
                    font-size: rem(14);
                    line-height: rem(14); } } } } }
.fp-section {
    .main-header {
        @include media-breakpoint-down(md) {
            position: absolute;
            left: 0px;
            top: 0px; } } }
