@keyframes arrows {
    from {
        transform: translateX(0); }
    50% {
        transform: translateX(-58px); }
    51% {
        transform: translateX(58px); }
    to {
        transform: translateX(0); } }

.default-link {
    .arrow-animate {}
    @include hover() {
        .arrow-animate {
            .arrows {
                animation: arrows 0.5s ease-in-out; } } } }

